.unleash-potential-main {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 208px 0px 208px 0px;
  background-image: url("../images/webp/diamond2_edited.webp");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.unleash-potential-main h3 {
  font-size: 50px;
  line-height: 72px;
  text-align: center;
  font-weight: 400;
}
.unleash-potential-main .button-div {
  margin: 42px 0px 42px 0px;
}
.unleash-potential-main p {
  margin: 42px 0px 42px 0px;
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
}
.unleash-potential-main .stars-section {
  width: 100%;
  display: grid;
  grid-template-columns: 35% 1fr 35%;
}
.unleash-potential-main .stars-section .col-one {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-column: 1/2;
}
.unleash-potential-main .stars-section .col-two {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-column: 2/3;
}
.unleash-potential-main .stars-section .col-three {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-column: 3/4;
}
.unleash-potential-main .stars-section p {
  text-align: center;
  font-size: 13px;
  line-height: 17px;
  font-weight: 400;
  margin-top: 15px;
  color: #BBBBBB;
}

@media (max-width: 1024px) {
  .unleash-potential-main {
    background-size: auto;
    padding: 100px 0px 100px 0px;
    margin: 100px 0px 100px 0px;
  }
  .unleash-potential-main h3 {
    font-size: 36px;
    line-height: 42px;
  }
  .unleash-potential-main .stars-section p {
    font-size: 10px;
  }
}
@media (max-width: 768px) {
  .unleash-potential-main {
    margin: 50px 0px 50px 0px;
    padding: 50px 0px 50px 0px;
  }
  .unleash-potential-main h3 {
    font-size: 20px;
    line-height: 27px;
  }
  .unleash-potential-main h3 br {
    display: none;
  }
  .unleash-potential-main p {
    font-size: 11px;
  }
  .unleash-potential-main .stars-section .col-one,
  .unleash-potential-main .stars-section .col-two,
  .unleash-potential-main .stars-section .col-three {
    grid-column: 1/4;
  }
  .unleash-potential-main .stars-section .col-one img,
  .unleash-potential-main .stars-section .col-two img,
  .unleash-potential-main .stars-section .col-three img {
    max-width: 90px;
  }
}/*# sourceMappingURL=UnleashPotential.css.map */