.icon-frame-s {
  border: 1px solid #474EFF;
  border-radius: 100px;
  padding-left: 12px;
  padding-right: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.icon-frame-s .icon-svg-s {
  width: 24px;
}
.icon-frame-s .icon-title-s {
  font-size: 22px;
  font-weight: 300;
  line-height: 34px;
  color: #474EFF;
}

@media (max-width: 1024px) {
  .icon-frame-s .icon-svg-s {
    width: 15px;
  }
  .icon-frame-s .icon-title-s {
    font-size: 15px;
    line-height: 25px;
  }
}/*# sourceMappingURL=IconFrameS.css.map */